import { StatusBar } from 'expo-status-bar';
import React, { useState, useEffect, useCallback, useContext } from 'react';

//library
import { StyleSheet, Text, View, ScrollView, TouchableOpacity, Image, Picker, Dimensions, TextInput, FlatList } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';


//const
const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

//icons
import Graph from '../assets/icons/Graph';
import User from '../assets/icons/User';
import Category from '../assets/icons/Category';
import Tovar from '../assets/icons/Tovar';
import Zakaz from '../assets/icons/Zakaz';


export default function Categorys({ navigation }) {

  const [loading, setLoading] = useState(true);
  const [load, setLoad] = useState(true);
  const [addmodal, setAddmodal] = useState(false);
  const [modalVisible, setModalVisible] = useState(false);
  const [dobav, setDobav] = useState(false);
  const [titled, setTitled] = useState('');
  const [id, setId] = useState('');


  const [title, setTitle] = useState('');
  const [titles, setTitles] = useState('');
  const [data, setData] = useState();
  const [das, setDas] = useState([]);
  const [poddata, setPodData] = useState();
  const [as, setAs] = useState(false);
  const [ud, setUd] = useState();

  let arr = [];

  const podcar = (id) => {
    setUd(id);
    fetch('https://api.dentaled.ru/catalog/podcat.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: id,
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson != null) {
          setPodData(responseJson);
          for (var i = 0; i <= responseJson.length; i++) {
            fest(responseJson[i].id);
          }
        } else {
          setPodData(null);
        }
      }).catch((error) => {
        console.error(error);
      })
      .finally(() => setLoad(false));   
  }
  const openModal = (id) => {
    setModalVisible(true);
    setId(id);
    podcar(id);
  }

  const category = (itemValue) => {
    fetch('https://api.dentaled.ru/catalog.php')
      .then((response) => response.json())
      .then((responseJson) => setData(responseJson))
      .catch((error) => console.error(error))
      .finally(() => setLoading(false));
  }


  const deletedCat = () => {
    fetch('https://api.dentaled.ru/catalog/deleteCat.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: id,
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          setModalVisible(false);
          category();
          setTitle('');
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });
  }

  const fet = () => {
    fetch('https://api.dentaled.ru/catalog/addCat.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        title: titled
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          setAddmodal(false);
          category();
          setTitled('');
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });
  }

  const delPod = (ids) => {
    fetch('https://api.dentaled.ru/catalog/deletePod.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: ids,
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          podcar(id);
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });
  }

  const addPoder = (ider) => {
    fetch('https://api.dentaled.ru/catalog/addPod.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: ider,
        title: titles
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          podcar(ider);
          setDobav(false);
          setTitles('');
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });
  }

  const [idPod, setIdPod] = useState();
  const [tes, seTes] = useState('');
  const [imas, setImas] = useState('');
  const addPodPoder = (id) => {
    setAs(true);
    setIdPod(id);
  }

  const adderPodser = () => {
    fetch('https://api.dentaled.ru/catalog/addPodPoder.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: idPod,
        title: tes,
        image: imas
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          podcar(ud);
          setAs(false);
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });
  }


  useEffect(() => {
    category();
  }, []);

  return (
    <View style={styles.container}>

    {as == true ?
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: '#00000075', position: 'absolute', zIndex: 3, width: '100%', height: '100%'}}>
        <View style={{width: 600, backgroundColor: '#292929', borderRadius: 5, }}>
          <View style={{padding: 20}}>
            <Text style={{color: '#fff', fontSize: 16}}>Добавление подкатегории</Text>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5 }}
                  onChangeText={seTes}
                  placeholderTextColor="#d3d3d3" 
                  value={tes}
                  placeholder="Название категории"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5 }}
                  onChangeText={setImas}
                  placeholderTextColor="#d3d3d3" 
                  value={imas}
                  placeholder="Ссылка на превью"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <TouchableOpacity onPress={() => adderPodser()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, marginTop: 20, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Добавить</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    :(
      <View></View>
    )}


    {modalVisible == true ?
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: '#00000075', position: 'absolute', zIndex: 2, width: '100%', height: '100%'}}>
        <View style={{width: 600, backgroundColor: '#292929', borderRadius: 5, }}>
          <View style={{padding: 20}}>
            <Text style={{color: '#fff', fontSize: 16}}>Редактирование категории</Text>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5 }}
                  onChangeText={setTitle}
                  placeholderTextColor="#d3d3d3" 
                  value={title}
                  placeholder="Название категории"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View>
            <View style={{marginTop: 20, flexDirection: 'row', justifyContent: 'space-between'}}>
              <Text style={{color: '#fff', fontSize: 16}}>Подкатегории</Text>
              <TouchableOpacity onPress={() => setDobav(true)}>
                  <Text style={{color: '#ff0000', fontSize: 14, fontWeight: '500'}}>Добавить</Text>
              </TouchableOpacity>
            </View>
            {dobav == true ?
                <View style={{marginTop: 20}}>
                  <TextInput
                    style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5 }}
                    onChangeText={setTitles}
                    placeholderTextColor="#d3d3d3" 
                    value={titles}
                    placeholder="Название подкатегории"
                    onSubmitEditing={Keyboard.dismiss}
                  />
                  <View style={{flexDirection: 'row', alignItems: 'center', marginTop: 20, justifyContent: 'flex-end'}}>
                    <TouchableOpacity onPress={() => addPoder(id)}>
                        <Text style={{color: '#ff0000', fontSize: 14, fontWeight: '500'}}>Добавить</Text>
                    </TouchableOpacity>
                    <TouchableOpacity onPress={() => setDobav(false)} style={{marginLeft: 30}}>
                        <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
                    </TouchableOpacity>
                  </View>
                </View>
              : (
                <View></View>
              )}
              {load == false ?
                <ScrollView style={{height: 300, marginTop: 20}}>
                  <FlatList
                    data={poddata}
                    showsVerticalScrollIndicator={false} 
                    renderItem={({ item }) => (
                      <View style={{backgroundColor: '#202020', marginTop: 20, borderRadius: 5, paddingBottom: 10}}>
                        <TouchableOpacity onPress={() => alert("Идентификатор категории: " + item.id)} style={{ padding: 10, marginLeft: 20, marginTop: 20, flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', borderRadius: 4}}>
                          <Text style={{color: '#fff', fontSize: 16, fontWeight: '600'}}>{item.title}</Text>
                          <TouchableOpacity onPress={() => delPod(item.id)}>
                              <Text style={{color: '#ff0000', fontSize: 14, fontWeight: '500'}}>Удалить</Text>
                          </TouchableOpacity>
                        </TouchableOpacity>
                        <View style={{padding: 10, marginLeft: 20, flexDirection: 'row', justifyContent: 'space-between'}}>
                          <Text style={{color: '#fff', fontSize: 14, opacity: 0.4}}>Категории</Text>
                          <TouchableOpacity onPress={() => navigation.navigate('Mincat', { id: item.id })}>
                              <Text style={{color: '#ff0000', fontSize: 14, fontWeight: '500'}}>Показать подкатегории</Text>
                          </TouchableOpacity>
                          <TouchableOpacity onPress={() => addPodPoder(item.id)}>
                              <Text style={{color: '#ff0000', fontSize: 14, fontWeight: '500'}}>Добавить</Text>
                          </TouchableOpacity>
                        </View>
                      </View>
                    )}
                    keyExtractor={item => item.id}
                  />
                </ScrollView>
              : (
                <View style={{marginTop: 20}}>
                  <Text style={{color: '#fff', opacity: 0.3}}>Загрузка...</Text>
                </View>
              )}
            </View>
          </View>
          <View style={{flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end', paddingRight: 20, paddingBottom: 20}}>
            <TouchableOpacity onPress={() => fetcher()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Обновить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => deletedCat()}  style={{paddingLeft: 30, marginLeft: 20, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Удалить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => setModalVisible(false)}  style={{padding: 10, marginLeft: 20, borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    :(
      <View></View>
    )}

    {addmodal == true ?
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: '#00000075', position: 'absolute', zIndex: 2, width: '100%', height: '100%'}}>
        <View style={{width: 600, height: 200, backgroundColor: '#292929', borderRadius: 5, }}>
          <View style={{padding: 20}}>
            <Text style={{color: '#fff', fontSize: 16}}>Добавление категории</Text>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5 }}
                  onChangeText={setTitled}
                  placeholderTextColor="#d3d3d3" 
                  value={titled}
                  placeholder="Название категории"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
          </View>
          <View style={{flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end', paddingRight: 20}}>
            <TouchableOpacity onPress={() => fet()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Добавить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => setAddmodal(false)}  style={{padding: 10, marginLeft: 20, borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    :(
      <View></View>
    )}

      <View style={styles.blockhain}>
        <View style={styles.menubar}>
          <View style={{marginTop: 70}}>
            <TouchableOpacity onPress={() => navigation.navigate('Home')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Graph />
              <Text style={{color: '#fff', marginLeft: 20}}>Главный экран</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Users')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <User />
              <Text style={{color: '#fff', marginLeft: 20}}>Пользователи</Text>
            </TouchableOpacity>
            <TouchableOpacity style={{padding: 10, marginTop: 10, borderRadius: 5, backgroundColor: '#ff0000', shadowColor: '#ff0000', shadowOpacity: 0.2, shadowRadius: 10, shadowOffset: {
              width: 5,
              height: 5
              },  width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Category />
              <Text style={{color: '#fff', marginLeft: 20, fontWeight: '600'}}>Категории</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('ShopItem')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Tovar />
              <Text style={{color: '#fff', marginLeft: 20}}>Товары</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Order')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Zakaz /> 
              <Text style={{color: '#fff', marginLeft: 20}}>Заказы</Text>
            </TouchableOpacity>
          </View>
        </View>
        <View style={styles.content}>
          <View style={{width: windowWidth/1.25, backgroundColor: '#252525', height: 70, justifyContent: 'center'}}>
            <Text style={{color: '#ff0000', fontWeight: '600', fontSize: 18, marginLeft: 20}}>DENTALED</Text>
          </View>
          <ScrollView style={{width: windowWidth/1.27, backgroundColor: '#202020', height: windowHeight, padding: 20}}>
            <View>
              <View>
                <Text style={{color: '#fff', fontSize: 18}}>Категории</Text>
              </View>
              {loading == false ?
                <View style={{flexDirection: 'row', flexWrap: 'wrap', alignItems: 'center', marginLeft: -20}}>
                  <FlatList
                    data={data}
                    contentContainerStyle={{
                        flexDirection: 'row',
                        flexWrap: 'wrap'
                    }}
                    showsVerticalScrollIndicator={false} 
                    renderItem={({ item }) => (
                      <TouchableOpacity onPress={() => openModal(item.id)} style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, marginLeft: 20, backgroundColor: '#292929', marginTop: 20, borderRadius: 4}}>
                        <Text style={{color: '#fff', fontSize: 16, fontWeight: '600'}}>{item.title}</Text>
                      </TouchableOpacity>
                    )}
                    keyExtractor={item => item.id}
                  />
                </View>
              : (
                <View style={{marginTop: 20}}>
                  <Text style={{color: '#fff', opacity: 0.3}}>Загрузка...</Text>
                </View>
              )}
              <View style={{marginTop: 30}}>
                <TouchableOpacity onPress={() => setAddmodal(true)} style={{padding: 10, backgroundColor: '#ff0000', borderRadius: 5, width: '20%', justifyContent: 'center', alignItems: 'center'}}>
                  <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Добавить категорию</Text>
                </TouchableOpacity>
              </View>
            </View>
          </ScrollView>
        </View>
      </View>
    </View>
  );
}


const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
  },
  blockhain: {
    flexDirection: 'row',
    alignItems: 'center'
  },
  menubar: {
    width: '20%',
    backgroundColor: '#252525',
    height: windowHeight,
    alignItems: 'center'
  }, 
  content: {
    width: '80%',
    backgroundColor: '#202020',
    height: windowHeight
  }
});