import { StatusBar } from 'expo-status-bar';
import React, { useState, useEffect, useCallback, useContext } from 'react';
import { useFocusEffect } from '@react-navigation/native';

//library
import { StyleSheet, Text, View, ScrollView, TouchableOpacity, Image, Picker, Dimensions, TextInput, FlatList } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';


//const
const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

//icons
import GraphActive from '../assets/icons/GraphActive';
import User from '../assets/icons/User';
import Category from '../assets/icons/Category';
import Tovar from '../assets/icons/Tovar';
import Zakaz from '../assets/icons/Zakaz';


export default function HomeScreen({ navigation }) {


  const [data, setData] = useState();
  const [dats, setDats] = useState();
  const [loading, setLoading] = useState(true);
  const [modalVisible, setModalVisible] = useState(false);
  const [addmodal, setAddmodal] = useState(false);
  const [addTovar, setaddTovar] = useState(false);
  const [selectedValue, setSelectedValue] = useState(1);

  const [title, setTitle] = useState('');
  const [titled, setTitled] = useState('');
  const [id, setId] = useState('');




  const [name, setName] = useState('');
  const [idshopitem, setIdshopitem] = useState('');
  const [price, setPrice] = useState('');
  const [sales, setSales] = useState('');
  const [about, setAbout] = useState('');
  const [image, setImage] = useState('');
  const [cat, setCat] = useState(0);

  const category = (itemValue) => {
    if (itemValue == null) {
    fetch('https://api.dentaled.ru/homecat.php')
      .then((response) => response.json())
      .then((responseJson) => setData(responseJson))
      .catch((error) => console.error(error))
      .finally(() => setLoading(false));

      fetch('https://api.dentaled.ru/home_item.php', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          category: selectedValue,
        })
      }).then((response) => response.json())
        .then((responseJson) => {
          setDats(responseJson);
        }).catch((error) => {
          console.error(error);
        });
    } else {
      fetch('https://api.dentaled.ru/homecat.php')
      .then((response) => response.json())
      .then((responseJson) => setData(responseJson))
      .catch((error) => console.error(error))
      .finally(() => setLoading(false));

      fetch('https://api.dentaled.ru/home_item.php', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          category: itemValue,
        })
      }).then((response) => response.json())
        .then((responseJson) => {
          setDats(responseJson);
        }).catch((error) => {
          console.error(error);
        });
    }
  }

  const openModal = (id) => {
    setModalVisible(true);
    setId(id);
  }

  const fetcher = () => {
    fetch('https://api.dentaled.ru/home/homecatider.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: id,
        title: title
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          setModalVisible(false);
          category();
          setTitle('');
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });
  }

  const deletedCat = () => {
    fetch('https://api.dentaled.ru/home/deletedCat.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: id,
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          setModalVisible(false);
          category();
          setTitle('');
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });
  }

  const fet = () => {
    fetch('https://api.dentaled.ru/home/addhome.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        title: titled
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          setAddmodal(false);
          category();
          setTitled('');
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });
  }

  const del = (id) => {
    fetch('https://api.dentaled.ru/home/delHome.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: id
      })
    }).then((response) => response.json())
      .then((responseJson) => {
          category();
      }).catch((error) => {
        console.error(error);
      });
  }

  const forer = () => {
    if (cat != null) {
    fetch('https://api.dentaled.ru/home/addShoper.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        title: name,
        price: price,
        idshopitem: idshopitem,
        sales: sales,
        about: about,
        cat: cat,
        image: image
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          setaddTovar(false);
          category();
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });
    } else {
      setCat(cat);
    }
  }

  useEffect(() => {
    category();
  }, []);


  return (
    <View style={styles.container}>
    {modalVisible == true ?
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: '#00000075', position: 'absolute', zIndex: 2, width: '100%', height: '100%'}}>
        <View style={{width: 600, height: 200, backgroundColor: '#292929', borderRadius: 5, }}>
          <View style={{padding: 20}}>
            <Text style={{color: '#fff', fontSize: 16}}>Редактирование категории</Text>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5 }}
                  onChangeText={setTitle}
                  placeholderTextColor="#d3d3d3" 
                  value={title}
                  placeholder="Название категории"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
          </View>
          <View style={{flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end', paddingRight: 20}}>
            <TouchableOpacity onPress={() => fetcher()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Обновить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => deletedCat()}  style={{paddingLeft: 30, marginLeft: 20, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Удалить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => setModalVisible(false)}  style={{padding: 10, marginLeft: 20, borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    :(
      <View></View>
    )}

    {addmodal == true ?
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: '#00000075', position: 'absolute', zIndex: 2, width: '100%', height: '100%'}}>
        <View style={{width: 600, height: 200, backgroundColor: '#292929', borderRadius: 5, }}>
          <View style={{padding: 20}}>
            <Text style={{color: '#fff', fontSize: 16}}>Добавление категории</Text>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5 }}
                  onChangeText={setTitled}
                  placeholderTextColor="#d3d3d3" 
                  value={titled}
                  placeholder="Название категории"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
          </View>
          <View style={{flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end', paddingRight: 20}}>
            <TouchableOpacity onPress={() => fet()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Добавить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => setAddmodal(false)}  style={{padding: 10, marginLeft: 20, borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    :(
      <View></View>
    )}

    {addTovar == true ?
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: '#00000075', position: 'absolute', zIndex: 2, width: '100%', height: '100%'}}>
        <View style={{width: 600, backgroundColor: '#292929', borderRadius: 5, }}>
          <View style={{padding: 20}}>
            <Text style={{color: '#fff', fontSize: 16}}>Добавление акции</Text>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setName}
                  placeholderTextColor="#d3d3d3" 
                  value={name}
                  placeholder="Название товара"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setPrice}
                  placeholderTextColor="#d3d3d3" 
                  value={price}
                  placeholder="Стоимость"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setIdshopitem}
                  placeholderTextColor="#d3d3d3" 
                  value={idshopitem}
                  placeholder="Идентификатор товара"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setImage}
                  placeholderTextColor="#d3d3d3" 
                  value={image}
                  placeholder="Ссылка на баннер"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setSales}
                  placeholderTextColor="#d3d3d3" 
                  value={sales}
                  placeholder="Скидка"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setAbout}
                  multiline = {true}
                  numberOfLines = {10}
                  placeholderTextColor="#d3d3d3" 
                  value={about}
                  placeholder="Описание"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
            <Text style={{color: '#fff', marginBottom: 20}}>Категория</Text>
              <Picker
                    selectedValue={cat}
                    style={{ width: 150, paddingTop: 10, paddingBottom: 10, paddingLeft: 20, paddingRight: 20, backgroundColor: '#202020', color: '#fff', borderWidth: 0, borderRadius: 5 }}
                    onValueChange={(itemValue, itemIndex) => {
                      setCat(itemValue); 
                    }}
                  >

                  {data != null ?
                    data.map((item, key)=>(
                        <Picker.Item label={item.title} value={item.id} key={key} />)
                    )
                  : (
                    <Picker.Item label="Загрузка" value="0" />
                  )}
                  </Picker>
            </View>
          </View>
          <View style={{flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end', paddingRight: 20, paddingBottom: 50}}>
            <TouchableOpacity onPress={() => forer()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Добавить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => setaddTovar(false)}  style={{padding: 10, marginLeft: 20, borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    :(
      <View></View>
    )}

      <View style={styles.blockhain}>
        <View style={styles.menubar}>
          <View style={{marginTop: 70}}>
            <TouchableOpacity style={{padding: 10, marginTop: 10, borderRadius: 5, backgroundColor: '#ff0000', shadowColor: '#ff0000', shadowOpacity: 0.2, shadowRadius: 10, shadowOffset: {
              width: 5,
              height: 5
            },  width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <GraphActive />
              <Text style={{color: '#fff', marginLeft: 20}}>Главный экран</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Users')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <User />
              <Text style={{color: '#fff', marginLeft: 20}}>Пользователи</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Categorys')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Category />
              <Text style={{color: '#fff', marginLeft: 20}}>Категории</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('ShopItem')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Tovar />
              <Text style={{color: '#fff', marginLeft: 20}}>Товары</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Order')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Zakaz /> 
              <Text style={{color: '#fff', marginLeft: 20}}>Заказы</Text>
            </TouchableOpacity>
          </View>
        </View>
        <View style={styles.content}>
          <View style={{width: windowWidth/1.25, backgroundColor: '#252525', height: 70, justifyContent: 'center'}}>
            <Text style={{color: '#ff0000', fontWeight: '600', fontSize: 18, marginLeft: 20}}>DENTALED</Text>
          </View>
          <ScrollView style={{width: windowWidth/1.27, backgroundColor: '#202020', height: windowHeight, padding: 20}}>
            <View>
              <View>
                <Text style={{color: '#fff', fontSize: 18}}>Категории</Text>
              </View>
              {loading == false ?
                <View style={{flexDirection: 'row', flexWrap: 'wrap', alignItems: 'center', marginLeft: -20}}>
                  <FlatList
                    data={data}
                    contentContainerStyle={{
                        flexDirection: 'row',
                        flexWrap: 'wrap'
                    }}
                    showsVerticalScrollIndicator={false} 
                    renderItem={({ item }) => (
                      <TouchableOpacity onPress={() => openModal(item.id)} style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, marginLeft: 20, backgroundColor: '#292929', marginTop: 20, borderRadius: 4}}>
                        <Text style={{color: '#fff', fontSize: 16, fontWeight: '600'}}>{item.title}</Text>
                      </TouchableOpacity>
                    )}
                    keyExtractor={item => item.id}
                  />
                </View>
              : (
                <View style={{marginTop: 20}}>
                  <Text style={{color: '#fff', opacity: 0.3}}>Загрузка...</Text>
                </View>
              )}
              <View style={{marginTop: 30}}>
                <TouchableOpacity onPress={() => setAddmodal(true)} style={{padding: 10, backgroundColor: '#ff0000', borderRadius: 5, width: '20%', justifyContent: 'center', alignItems: 'center'}}>
                  <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Добавить категорию</Text>
                </TouchableOpacity>
              </View>
            </View>
            <View style={{marginTop: 50}}>
              <View>
                <Text style={{color: '#fff', fontSize: 18}}>Акции (с главной страницы)</Text>
              </View>
              <View style={{marginTop: 20}}>
                <Text style={{color: '#ffff'}}>Выберите категорию</Text>
              </View>
              <View style={{marginTop: 20}}>
                  <View>
                    <Picker
                      selectedValue={selectedValue}
                      style={{ width: 150, paddingTop: 10, paddingBottom: 10, paddingLeft: 20, paddingRight: 20, backgroundColor: '#292929', color: '#fff', borderWidth: 0, borderRadius: 5 }}
                      onValueChange={(itemValue, itemIndex) => {
                        setSelectedValue(itemValue); 
                        category(itemValue);
                      }}
                    >

                    {data != null ?
                      data.map((item, key)=>(
                          <Picker.Item label={item.title} value={item.id} key={key} />)
                      )
                    : (
                      <Picker.Item label="Загрузка" value="0" />
                    )}
                    </Picker>
                  </View>
                <View style={{marginTop: 30}}>
                  <TouchableOpacity onPress={() => setaddTovar(true)} style={{padding: 10, backgroundColor: '#ff0000', borderRadius: 5, width: '20%', justifyContent: 'center', alignItems: 'center'}}>
                    <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Добавить акцию (главный экран)</Text>
                  </TouchableOpacity>
                </View>
              </View>
              <View style={{marginTop: 50, flexDirection: 'row', alignItems: 'flex-start', marginLeft: -20 }}>
              {dats != null ?
                dats.map((item, key)=>(
                  <View style={{width: 200, marginLeft: 20, backgroundColor: '#292929', borderRadius: 10}}>
                      <Image source={{uri: item.image}} style={{width: '100%', height: 150, borderTopLeftRadius: 10, borderTopRightRadius: 10}} />
                      <Text style={{margin: 20, color: '#fff'}}>{item.title}</Text>
                      <TouchableOpacity onPress={() => del(item.id)} style={{padding: 10, marginTop: 10, backgroundColor: '#ff0000', borderRadius: 5, alignItems: 'center', justifyContent: 'center'}}>
                        <Text style={{color: '#fff', fontSize: 15, fontWeight: '600'}}>Удалить</Text>
                      </TouchableOpacity>
                  </View> 
                ))
              : (
                  <View></View>
              )}
              </View>
            </View>
          </ScrollView>
        </View>
      </View>
    </View>
  );
}


const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
  },
  blockhain: {
    flexDirection: 'row',
    alignItems: 'center'
  },
  menubar: {
    width: '20%',
    backgroundColor: '#252525',
    height: windowHeight,
    alignItems: 'center'
  }, 
  content: {
    width: '80%',
    backgroundColor: '#202020',
    height: windowHeight
  }
});
