import { StatusBar } from 'expo-status-bar';
import React, { useState, useEffect, useCallback, useContext } from 'react';
import { useFocusEffect } from '@react-navigation/native';

//library
import { StyleSheet, Text, View, ScrollView, TouchableOpacity, Image, Picker, Dimensions, TextInput, FlatList } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';

import * as DocumentPicker from 'expo-document-picker';

//const
const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

//icons
import Graph from '../assets/icons/Graph';
import User from '../assets/icons/User';
import Category from '../assets/icons/Category';
import Tovar from '../assets/icons/Tovar';
import Zakaz from '../assets/icons/Zakaz';


export default function ShopItem({ navigation }) {

  const [text, setText] = useState('');
  const [data, setData] = useState();
  const [dats, setDats] = useState();
  const [tov, setTov] = useState();
  const [sal, setSal] = useState();
  const [nal, setNal] = useState();
  const [modalVisible, setModalVisible] = useState(false);

  const [salesing, setSalesing] = useState(false);
  const [nat, setNat] = useState(false);

  const [selectedValue, setSelectedValue] = useState(1);

  const [name, setName] = useState('');
  const [idshopitem, setIdshopitem] = useState('');
  const [price, setPrice] = useState('');
  const [sales, setSales] = useState('');
  const [about, setAbout] = useState('');
  const [image, setImage] = useState('');
  const [pressed, setPressed] = useState('');
  const [cat, setCat] = useState(1);

  const fetcher = () => {
    fetch('https://api.dentaled.ru/shoper/addyml.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        text: text,
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          alert('Отправлено!')
        }
      }).catch((error) => {
        console.error(error);
      });
  }

  const tovars = () => {
      fetch('https://api.dentaled.ru/shoper/shopser.php')
      .then((response) => response.json())
      .then((responseJson) => setData(responseJson))
      .catch((error) => console.error(error))

      fetch('https://api.dentaled.ru/shoper/cat.php')
      .then((response) => response.json())
      .then((responseJson) => setDats(responseJson))
      .catch((error) => console.error(error))
  }

  const ser = () => {
    fetch('https://api.dentaled.ru/shoper/addTovar.php', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          id_catalog: cat,
          title: name,
          price: price,
          sales: sales,
          pressed: pressed,
          about: about,
          model: idshopitem,
          image: image
        })
      }).then((response) => response.json())
        .then((responseJson) => {
          setModalVisible(false);
          tovars();
        }).catch((error) => {
          console.error(error);
        });
  }
  
  const openModal = () => {
    setModalVisible(true);
  }

  const fet = () => {
    fetch('https://api.dentaled.ru/shoper/salesing.php', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          sales: sal,
        })
      }).then((response) => response.json())
        .then((responseJson) => {
          setSalesing(false);
          tovars();
        }).catch((error) => {
          console.error(error);
        });
  }

  const net = () => {
    fetch('https://api.dentaled.ru/shoper/uvel.php', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          sales: nal,
        })
      }).then((response) => response.json())
        .then((responseJson) => {
          setNat(false);
          tovars();
        }).catch((error) => {
          console.error(error);
        });
  }

  const del = (id) => {
    fetch('https://api.dentaled.ru/shoper/delTov.php', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          id: id
        })
      }).then((response) => response.json())
        .then((responseJson) => {
          alert('Удалено');
          tovars();
        }).catch((error) => {
          console.error(error);
        });
  }

  useEffect(() => {
    tovars();
  }, []);

  return (
    <View style={styles.container}>
    {salesing == true ?
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: '#00000075', position: 'absolute', zIndex: 2, width: '100%', height: '100%'}}>
        <View style={{width: 600, height: 200, backgroundColor: '#292929', borderRadius: 5, }}>
          <View style={{padding: 20}}>
            <Text style={{color: '#fff', fontSize: 16}}>Скидка на товары</Text>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5 }}
                  onChangeText={setSal}
                  placeholderTextColor="#d3d3d3" 
                  value={sal}
                  placeholder="Укажите процент скидки"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
          </View>
          <View style={{flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end', paddingRight: 20}}>
            <TouchableOpacity onPress={() => fet()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отправить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => setSalesing(false)}  style={{padding: 10, marginLeft: 20, borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    :(
      <View></View>
    )}


    {nat == true ?
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: '#00000075', position: 'absolute', zIndex: 2, width: '100%', height: '100%'}}>
        <View style={{width: 600, height: 200, backgroundColor: '#292929', borderRadius: 5, }}>
          <View style={{padding: 20}}>
            <Text style={{color: '#fff', fontSize: 16}}>Наценка на товары</Text>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5 }}
                  onChangeText={setNal}
                  placeholderTextColor="#d3d3d3" 
                  value={nal}
                  placeholder="Укажите процент наценки"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
          </View>
          <View style={{flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end', paddingRight: 20}}>
            <TouchableOpacity onPress={() => net()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отправить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => setNat(false)}  style={{padding: 10, marginLeft: 20, borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    :(
      <View></View>
    )}

    {modalVisible == true ?
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: '#00000075', position: 'absolute', zIndex: 2, width: '100%', height: '100%'}}>
        <View style={{width: 600, backgroundColor: '#292929', borderRadius: 5, }}>
          <View style={{padding: 20}}>
            <Text style={{color: '#fff', fontSize: 16}}>Добавление товара</Text>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setName}
                  placeholderTextColor="#d3d3d3" 
                  value={name}
                  placeholder="Название товара"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setPrice}
                  placeholderTextColor="#d3d3d3" 
                  value={price}
                  placeholder="Стоимость"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setIdshopitem}
                  placeholderTextColor="#d3d3d3" 
                  value={idshopitem}
                  placeholder="Модель инструмента"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setImage}
                  placeholderTextColor="#d3d3d3" 
                  value={image}
                  placeholder="Ссылка на изображение"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setSales}
                  placeholderTextColor="#d3d3d3" 
                  value={sales}
                  placeholder="Скидка"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setAbout}
                  multiline = {true}
                  numberOfLines = {10}
                  placeholderTextColor="#d3d3d3" 
                  value={about}
                  placeholder="Описание"
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
            </View>
            <View style={{marginTop: 20}}>
            <Text style={{color: '#fff', marginBottom: 20}}>Категория</Text>
              <Picker
                    selectedValue={cat}
                    style={{ width: 150, paddingTop: 10, paddingBottom: 10, paddingLeft: 20, paddingRight: 20, backgroundColor: '#202020', color: '#fff', borderWidth: 0, borderRadius: 5 }}
                    onValueChange={(itemValue, itemIndex) => {
                      setCat(itemValue); 
                    }}
                  >

                  {dats != null ?
                    dats.map((item, key)=>(
                        <Picker.Item label={item.title} value={item.id} key={key} />)
                    )
                  : (
                    <Picker.Item label="Загрузка" value="0" />
                  )}
                  </Picker>
            </View>
          </View>
          <View style={{flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end', paddingRight: 20, paddingBottom: 50}}>
            <TouchableOpacity onPress={() => ser()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Добавить</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => setModalVisible(false)}  style={{padding: 10, marginLeft: 20, borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    :(
      <View></View>
    )}

      <View style={styles.blockhain}>
        <View style={styles.menubar}>
          <View style={{marginTop: 70}}>
            <TouchableOpacity onPress={() => navigation.navigate('Home')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Graph />
              <Text style={{color: '#fff', marginLeft: 20}}>Главный экран</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Users')}style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <User />
              <Text style={{color: '#fff', marginLeft: 20}}>Пользователи</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Categorys')}style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Category />
              <Text style={{color: '#fff', marginLeft: 20}}>Категории</Text>
            </TouchableOpacity>
            <TouchableOpacity style={{padding: 10, marginTop: 10, borderRadius: 5, backgroundColor: '#ff0000', shadowColor: '#ff0000', shadowOpacity: 0.2, shadowRadius: 10, shadowOffset: {
              width: 5,
              height: 5
            },  width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Tovar />
              <Text style={{color: '#fff', marginLeft: 20}}>Товары</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Order')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Zakaz /> 
              <Text style={{color: '#fff', marginLeft: 20}}>Заказы</Text>
            </TouchableOpacity>
          </View>
        </View>
        <View style={styles.content}>
          <View style={{width: windowWidth/1.25, backgroundColor: '#252525', height: 70, justifyContent: 'center'}}>
            <Text style={{color: '#ff0000', fontWeight: '600', fontSize: 18, marginLeft: 20}}>DENTALED</Text>
          </View>
          <ScrollView style={{width: windowWidth/1.27, backgroundColor: '#202020', height: windowHeight, padding: 20}}>
            <View>
              <Text style={{color: '#fff', fontSize: 18}}>Товары</Text>
            </View>
            <View style={{marginTop: 30}}>
              <View>
                <TextInput
                  style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                  onChangeText={setText}
                  placeholderTextColor="#d3d3d3" 
                  value={text}
                  placeholder="КОД YML"
                  multiline={true}
                  numberOfLines={10}
                  onSubmitEditing={Keyboard.dismiss}
                />
              </View>
              <TouchableOpacity onPress={() => fetcher()} style={{padding: 15, marginTop: 30, width: '15%', backgroundColor: '#ff0000', borderRadius: 5}}>
                <Text style={{color: '#fff', fontWeight: '500', textAlign: 'center'}}>Отправить</Text>
              </TouchableOpacity>
              <View style={{marginTop: 40}}>
                <Text style={{color: '#fff', fontSize: 18}}>Добавление товаров вручную</Text>
              </View>
              <View style={{flexDirection: 'row', alignItems: 'center'}}>
                <TouchableOpacity onPress={() => openModal()} style={{padding: 15, marginTop: 20, width: '15%', backgroundColor: '#ff0000', borderRadius: 5}}>
                  <Text style={{color: '#fff', fontWeight: '500', textAlign: 'center'}}>Добавить товар</Text>
                </TouchableOpacity>
                <TouchableOpacity onPress={() => setSalesing(true)} style={{padding: 15, marginLeft: 20, marginTop: 20, width: '15%', backgroundColor: '#ff0000', borderRadius: 5}}>
                  <Text style={{color: '#fff', fontWeight: '500', textAlign: 'center'}}>Скидка</Text>
                </TouchableOpacity>
                <TouchableOpacity onPress={() => setNat(true)} style={{padding: 15, marginLeft: 20, marginTop: 20, width: '15%', backgroundColor: '#ff0000', borderRadius: 5}}>
                  <Text style={{color: '#fff', fontWeight: '500', textAlign: 'center'}}>Наценка</Text>
                </TouchableOpacity>
              </View>
              <View style={{marginTop: 50, flexDirection: 'row', alignItems: 'flex-start', flexWrap: 'wrap', marginLeft: -20 }}>
              {data != null ?
                data.map((item, key)=>(
                  <TouchableOpacity onPress={() => navigation.navigate('Tovars', {id: item.id})} style={{width: 200, marginLeft: 20, marginTop: 20, backgroundColor: '#292929', borderRadius: 10}}>
                      <Image source={{uri: item.image}} style={{width: '100%', height: 150, borderTopLeftRadius: 10, borderTopRightRadius: 10}} />
                      <Text style={{margin: 20, color: '#fff'}}>{item.title}</Text>
                      <View style={{paddingLeft: 20, borderRadius: 5}}>
                        <Text style={{color: '#fff', fontSize: 15, fontWeight: '600'}}>{item.price}</Text>
                      </View>
                      <TouchableOpacity onPress={() => del(item.id)} style={{padding: 10, marginTop: 20, backgroundColor: '#ff0000', borderRadius: 5, alignItems: 'center', justifyContent: 'center'}}>
                        <Text style={{color: '#fff', fontSize: 15, fontWeight: '600'}}>Удалить</Text>
                      </TouchableOpacity>
                  </TouchableOpacity> 
                ))
              : (
                  <View>
                    <Text style={{color: '#fff'}}>Загрузка</Text>
                  </View>
              )}
              </View>
            </View>
          </ScrollView>
        </View>
      </View>
    </View>
  );
}


const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
  },
  blockhain: {
    flexDirection: 'row',
    alignItems: 'center'
  },
  menubar: {
    width: '20%',
    backgroundColor: '#252525',
    height: windowHeight,
    alignItems: 'center'
  }, 
  content: {
    width: '80%',
    backgroundColor: '#202020',
    height: windowHeight
  }
});