import { StatusBar } from 'expo-status-bar';
import React from 'react';
import { StyleSheet, Text, View, TouchableOpacity } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';

//library
import Home from './screens/Home';
import Users from './screens/Users';
import Categorys from './screens/Categorys';
import Order from './screens/Order';
import ShopItem from './screens/ShopItem';
import Tovars from './screens/Tovars';
import Mincat from './screens/Mincat';

const Stack = createStackNavigator();


function App() {
  return (
    <NavigationContainer>
      <Stack.Navigator>
        <Stack.Screen name="Home" component={Home} options={{ headerShown: false }}/>
        <Stack.Screen name="Users" component={Users} options={{ headerShown: false }}/>
        <Stack.Screen name="Categorys" component={Categorys} options={{ headerShown: false }}/>
        <Stack.Screen name="Order" component={Order} options={{ headerShown: false }}/>
        <Stack.Screen name="ShopItem" component={ShopItem} options={{ headerShown: false }}/>
        <Stack.Screen name="Tovars" component={Tovars} options={{ headerShown: false }}/>
        <Stack.Screen name="Mincat" component={Mincat} options={{ headerShown: false }}/>
      </Stack.Navigator>
    </NavigationContainer>
  );
}

export default App;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
