import { StatusBar } from 'expo-status-bar';
import React, { useState, useEffect, useCallback, useContext } from 'react';
import { useFocusEffect } from '@react-navigation/native';

//library
import { StyleSheet, Text, View, ScrollView, TouchableOpacity, Image, Picker, Dimensions, TextInput, FlatList } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';


//const
const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

//icons
import Graph from '../assets/icons/Graph';
import User from '../assets/icons/User';
import Category from '../assets/icons/Category';
import Tovar from '../assets/icons/Tovar';
import ZakazActive from '../assets/icons/ZakazActive';


export default function Order({ navigation }) {

  const [data, setData] = useState();
  const [dataTwo, setDataTwo] = useState();
  const [loading, setLoading] = useState(true);

  const users = () => {
    fetch('https://api.dentaled.ru/order/setorder.php')
      .then((response) => response.json())
      .then((responseJson) => {
        setData(responseJson);
        for (let i = 0; i < responseJson.length; i++) {
          setDataTwo(responseJson[i].product);
        }
      })
      .catch((error) => console.error(error))
      .finally(() => setLoading(false));
  }

  const deleting = (id) => {

    fetch('https://api.dentaled.ru/order/deleteOrder.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: id,
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          users();
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });

  }

  useEffect(() => {
    users();
  }, []);

  return (
    <View style={styles.container}>
      <View style={styles.blockhain}>
        <View style={styles.menubar}>
          <View style={{marginTop: 70}}>
            <TouchableOpacity onPress={() => navigation.navigate('Home')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Graph />
              <Text style={{color: '#fff', marginLeft: 20}}>Главный экран</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Users')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <User />
              <Text style={{color: '#fff', marginLeft: 20}}>Пользователи</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Categorys')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Category />
              <Text style={{color: '#fff', marginLeft: 20}}>Категории</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('ShopItem')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Tovar />
              <Text style={{color: '#fff', marginLeft: 20}}>Товары</Text>
            </TouchableOpacity>
            <TouchableOpacity style={{padding: 10, marginTop: 10, borderRadius: 5, backgroundColor: '#ff0000', shadowColor: '#ff0000', shadowOpacity: 0.2, shadowRadius: 10, shadowOffset: {
              width: 5,
              height: 5
            },  width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <ZakazActive />
              <Text style={{color: '#fff', marginLeft: 20}}>Заказы</Text>
            </TouchableOpacity>
          </View>
        </View>
        <View style={styles.content}>
          <View style={{width: windowWidth/1.25, backgroundColor: '#252525', height: 70, justifyContent: 'center'}}>
            <Text style={{color: '#ff0000', fontWeight: '600', fontSize: 18, marginLeft: 20}}>DENTALED</Text>
          </View>
          <ScrollView style={{width: windowWidth/1.27, backgroundColor: '#202020', height: windowHeight, padding: 20}}>
            <View>
                <Text style={{color: '#fff', fontSize: 18}}>Заказы</Text>
            </View>
            <View>
            {data != null ? 
            <FlatList
              data={data}
              showsVerticalScrollIndicator={false} 
              renderItem={({ item }) => (
                <View style={{backgroundColor: '#292929', borderRadius: 5, width: '100%', padding: 20, marginTop: 40}}>
                  <View>
                    <Text style={{color: '#fff', fontSize: 18, fontWeight: '700'}}>{item.title}</Text>
                  </View>
                  <View style={{marginTop: 40, flexDirection: 'row', alignItems: 'center', padding: 15, backgroundColor: '#202020', borderRadius: 5}}>
                    <Text style={{color: '#fff', fontSize: 15}}>Имя клиента: <Text style={{color: '#ff0000'}}>{item.name}</Text></Text>
                    <Text style={{color: '#fff', fontSize: 15, marginLeft: 30}}>Телефон: <Text style={{color: '#ff0000'}}>{item.tel}</Text></Text>
                    <Text style={{color: '#fff', fontSize: 15, marginLeft: 30}}>e-mail: <Text style={{color: '#ff0000'}}>{item.email}</Text></Text>
                  </View>
                  <View style={{marginTop: 30, flexDirection: 'row', alignItems: 'center', padding: 15, backgroundColor: '#202020', borderRadius: 5}}>
                    <Text style={{color: '#fff', fontSize: 15}}>Город: <Text style={{color: '#ff0000'}}>{item.city}</Text></Text>
                    <Text style={{color: '#fff', fontSize: 15, marginLeft: 30}}>Адрес: <Text style={{color: '#ff0000'}}>{item.adress}</Text></Text>
                    <Text style={{color: '#fff', fontSize: 15, marginLeft: 30}}>Дата: <Text style={{color: '#ff0000'}}>{item.date}</Text></Text>
                    <Text style={{color: '#fff', fontSize: 15, marginLeft: 30}}>Способ доставки: <Text style={{color: '#ff0000'}}>{item.dostavka}</Text></Text>
                  </View>
                  <Text style={{color: '#fff', marginTop: 30}}>Комментарий</Text>
                  <View style={{marginTop: 30, flexDirection: 'row', alignItems: 'center', padding: 15, backgroundColor: '#202020', borderRadius: 5}}>
                    <Text style={{color: '#fff', fontSize: 15}}>{item.comment}</Text>
                  </View>
                  <Text style={{color: '#fff', marginTop: 30}}>Список товаров</Text>
                  {item.product != null ?
                    JSON.parse(item.product).map((item, key)=>(
                      <View style={{marginTop: 30, flexDirection: 'row', alignItems: 'center', padding: 15, backgroundColor: '#202020', borderRadius: 5}}>
                        <Text style={{color: '#fff', fontSize: 15}}>Название товара: <Text style={{color: '#ff0000'}}>{item.title}</Text></Text>
                        <Text style={{color: '#fff', fontSize: 15, marginLeft: 30}}>Количество: <Text style={{color: '#ff0000'}}>{item.count}</Text></Text>
                        <Text style={{color: '#fff', fontSize: 15, marginLeft: 30}}>Цена: <Text style={{color: '#ff0000'}}>{item.price}</Text></Text>
                      </View>
                    )) 
                  : (
                    <View>
                      <Text style={{color: '#fff', opacity: 0.4}}>Ошибка</Text>
                    </View>
                  )}
                  <Text style={{color: '#fff', marginTop: 30}}>Итоговая стоимость: {item.price}</Text>
                  <TouchableOpacity onPress={() => deleting(item.id)}  style={{padding: 10, width: '25%', marginTop: 30, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                      <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Удалить</Text>
                  </TouchableOpacity>
                </View>
              )}
                keyExtractor={item => item.id}
              />
              : (
                <View style={{marginTop: 20}}>
                  <Text style={{color: '#fff', opacity: 0.3}}>Нет не обработанных заказов</Text>
                </View>
              )}
            </View>
          </ScrollView>
        </View>
      </View>
    </View>
  );
}


const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
  },
  blockhain: {
    flexDirection: 'row',
    alignItems: 'center'
  },
  menubar: {
    width: '20%',
    backgroundColor: '#252525',
    height: windowHeight,
    alignItems: 'center'
  }, 
  content: {
    width: '80%',
    backgroundColor: '#202020',
    height: windowHeight
  }
});